.App {
  text-align: left;
  margin-left: 50px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App .dash{
  height: var(--nav-size);
  background-color: var(--bg);
  padding: 0 1rem;
  borde-bottom: var(--border);
  position: fixed;
}
.App .subdash{
  display: flex;
  justify-content: flex-end;
  position: absolute;
  top: 5px;
  left: 910px;
  position: fixed;
}

.App .dashdown{
  display: flex;
  position: absolute;
  top: 7%;
}

.App label{
  text-align: right;
  justify-content: flex-end;
  
}

.App select{
  text-align: right;
  justify-content: flex-end;
  margin: 7px;
  border-radius: 5px;
  
}

.App .memberdetails{
  margin-left: 100px;
}

.App .mul{
  position: absolute;
  top: 70%;
  left: 460%;
}

.App .main button{
width: 150px;  
height: 21px;
cursor: pointer;
}